import React from "react";
import useStyles from "./style";
import DeleteIcon from "@material-ui/icons/Delete";
import MoreHorizIcon from "@material-ui/icons/MoreHoriz";
import ThumbUpAltIcon from "@material-ui/icons/ThumbUpAlt";
import moment from "moment";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import { useDispatch, useSelector } from "react-redux";
import { deletePost, likePost } from "../../../actions/posts";

const Post = ({ post, setCurrentId }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const user = useSelector((state) => state.auth.authData);
  const Likes = () => {
    if (post.likeCount.length > 0) {
      return post.likeCount.find(
        (like) => like === (user?.result?.googleId || user?.result?._id)
      ) ? (
        <>
          <ThumbUpAltIcon fontSize="small" /> &nbsp;
          {post.likeCount.length > 1
            ? `You and ${post.likeCount.length - 1} others`
            : `You Liked`}
        </>
      ) : (
        <>
          <ThumbUpAltIcon fontSize="small" /> &nbsp;{post.likeCount.length}{" "}
          {post.likeCount.length > 1 ? "Likes" : "Like"}
        </>
      );
    }
    return (
      <>
        <ThumbUpAltIcon fontSize="small" />
        &nbsp; Like{" "}
      </>
    );
  };
  const isCreator =
    post?.creator === user?.result?.googleId ||
    post?.creator === user?.result?._id;

  return (
    // <Card className={classes.card}>
    //   <CardMedia
    //     className={classes.media}
    //     image={post.selectedFile}
    //     title={post.title}
    //   />
    //   <div className={classes.overlay}>
    //     <Typography variant="h6">{post.creator}</Typography>
    //     <Typography variant="body2">
    //       {moment(post.createdAt).fromNow()}
    //     </Typography>
    //     <div className={classes.overlay2}>
    //       <Button style={{ color: "white" }} size="small" onClick={() => {}}>
    //         <MoreHorizIcon fontSize="default" />
    //       </Button>
    //     </div>

    //     <CardContent>
    //       <div className={classes.details}>
    //         <Typography variant="body2" color="textSecondary">
    //           {post.tags.map((tag) => `#${tag} `)}
    //         </Typography>
    //       </div>
    //       <Typography variant="h5" gutterBottom className={classes.title}>
    //         {post.message}
    //       </Typography>
    //     </CardContent>
    //     <CardActions className={classes.cardActions}>
    //       <Button size="small" color="primary" onClick={() => {}}>
    //         <ThumbUpAltIcon fontSize="small" /> Like {post.likeCount}
    //       </Button>
    //       <Button size="small" color="primary" onClick={() => {}}>
    //         <DeleteIcon fontSize="small" /> Delete
    //       </Button>
    //     </CardActions>
    //   </div>
    // </Card>
    <Card className={classes.card}>
      <CardMedia
        className={classes.media}
        image={post.selectedFile}
        title={post.title}
      />
      <div className={classes.overlay}>
        <Typography variant="h6">{post.name}</Typography>
        <Typography variant="body2">
          {moment(post.createdAt).fromNow()}
        </Typography>
      </div>
      {isCreator && (
        <div className={classes.overlay2}>
          <Button
            style={{ color: "white" }}
            size="small"
            onClick={() => setCurrentId(post._id)}
          >
            <MoreHorizIcon fontSize="medium" />
          </Button>
        </div>
      )}
      <CardContent>
        <Typography gutterBottom variant="h5" component="h2">
          {post.title}
        </Typography>
        <Typography variant="body2" component="h2">
          {post.message}
        </Typography>
        <Typography variant="body2" color="textSecondary">
          {post.tags && post.tags.map((tag) => `#${tag} `)}
        </Typography>
      </CardContent>
      <CardActions className={classes.cardActions}>
        <Button
          size="small"
          color="primary"
          disabled={!user?.result}
          onClick={() => dispatch(likePost(post._id))}
        >
          {/* <ThumbUpAltIcon fontSize="small" /> {`Like ${post.likeCount.length}`} */}
          <Likes />
        </Button>
        {isCreator && (
          <Button
            size="small"
            color="primary"
            onClick={() => dispatch(deletePost(post._id))}
          >
            <DeleteIcon fontSize="small" /> Delete
          </Button>
        )}
      </CardActions>
    </Card>
  );
};

export default Post;
