import {
  FETCH_ALL,
  CREATE,
  DELETE,
  UPDATE,
  LIKE,
} from "../constants/actionTypes";
export default (state = [], action) => {
  switch (action.type) {
    case FETCH_ALL:
      return action.payload;
    case CREATE:
      return [...state, action.payload];
    case UPDATE:
    case LIKE:
      const newState = state.map((post) =>
        post._id === action.payload._id ? action.payload : post
      );
      return newState;
    case DELETE:
      const newState2 = state.filter((post) => post._id !== action.payload.id);
      return newState2;
    default:
      return state;
  }
};
